import Vue from 'vue';
import Router from 'vue-router';
import { Toast } from "vant";
Vue.use(Router);

const routes = [{
    path: '*',
    redirect: '/memberCard'
  },
  {
    name: 'user',
    component: () => import('./view/user'),
    meta: {
      title: '会员中心'
    }
  },
  {
    name: 'cart',
    component: () => import('./view/cart'),
    meta: {
      title: '购物车'
    }
  },
  {
    name: 'home',
    component: () => import('./view/home'),
    meta: {
      title: '主页'
    }
  },
  {
    name: 'login',
    component: () => import('./view/login'),
    meta: {
      title: '登录'
    }
  },
  {
    name: 'payment',
    component: () => import('./view/payment'),
    meta: {
      title: '充值'
    }
  },
  {
    name: 'Map',
    component: () => import('./view/map'),
    meta: {
      title: '附近门店'
    }
  },
  {
    name: 'record',
    component: () => import('./view/record'),
    meta: {
      title: '交易记录'
    }
  },
  {
    name: 'memberCard',
    component: () => import('./view/memberCard'),
    meta: {
      title: '我的会员卡'
    }
  },
  {
    name: 'choosingStore',
    component: () => import('./view/choosingStore'),
    meta: {
      title: '选择服务门店'
    }
  },
  {
    name: 'city',
    component: () => import('./view/city'),
    meta: {title: '城市列表'}
  }
];

// add route path
routes.forEach(route => {
  route.path = route.path || '/' + (route.name || '');
});

const router = new Router({
  routes
});

router.beforeEach((to, from, next) => {
  const title = to.meta && to.meta.title;
  if (title) {
    document.title = title;
  }
  if (to.path === '/login') {
    next()
  } else {
    // 如果去的不是login
    // 判断token token存在next()

    // const token = localStorage.getItem('token')
    // if (!token) {
    //   Toast('请先登录');
    //   // 回到登录
    //   router.push({
    //     name: 'login'
    //   })
    //   return
    // }
    next()
  }
  next();
});

