import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let defaultCity = {}
let defaultStore = {}


try {
    if (localStorage.city) {
        defaultCity = localStorage.city
    }
    if (localStorage.store) {
        defaultStore = localStorage.store
    }
} catch (e) {
    e
}

const state = {
    count: 1,
    city: defaultCity,
    store:defaultStore
}

const mutations = {
    increment(state, param) {
        state.count += param
    },
    decrement(state) {
        state.count--
    },
    changeCity(state, city) {
        state.city = city
        try {
            localStorage.city = city
        } catch (e) {
            e
        }
    },
    changeStore(state, store) {
        state.store = store
        try {
            localStorage.store = store
        } catch (e) {
            e
        }
    }
}

const actions = {
    increment: ({
        commit
    }, param) => {
        commit('increment', param)
    },
    decrement: ({
        commit
    }) => {
        commit('decrement')
    }
}

export default new Vuex.Store({
    state,
    mutations,
    actions
})